import React, { Component } from 'react'
import { Menu } from 'semantic-ui-react'
import { Link } from 'react-router-dom';

export class head extends Component {

  render() {
    return (
      <Menu inverted className='bridgeV2User' style={{
        maxWidth: '500px', margin: '2px auto auto auto',
        background: 'rgba(30, 87, 79)', borderRadius: '10px'
      }}>
        <Link to="/BridgeQueryV2">
          <Menu.Item
            name='查询'
          />
        </Link>
        <Link to="/BridgeSendV2">
          <Menu.Item
            name='发送'
          />
        </Link>
        <Menu.Menu position='right'>
          <div style={{display:'none'}}>
            <Link to="/BridgeCreate">
              <Menu.Item
                name='LG'
              />
            </Link>
          </div>
          <Link to="/BridgeCreateV2">
            <Menu.Item
              name='添加'
            />
          </Link>
          <Link to="/BridgeManageV2">
            <Menu.Item
              name='管理'
            />
          </Link>
        </Menu.Menu>
      </Menu>
    )
  }
}

export default head
