import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import web3 from '../../../ABI/web3';
import { Link, withRouter } from 'react-router-dom';
import { getChainInfo, isApprove, SelectWeb3 } from '../../../components/global/functions';
import Logo from '../../../logo192.png';
import HECOIcon from '../../../img/hecochain-logo.svg';
import OECIcon from '../../../img/oec-logo.png';
import BSCIcon from '../../../img/bsc-logo.svg';
import HangQinAbi from '../../../ABI/hangQin.json';
const BigNumber = require('bignumber.js');

export class LGearn extends Component {
  constructor(props) {
    super(props)
    this.state = {
      LGprice: {
        bsc:'',
        heco:'',
        oec:''
      }
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr) {
      await this.getLGprice()
    }
  }

  async componentDidMount() {
    console.log(this.state.LGprice)
    await this.getLGprice()
    console.log(this.state.LGprice)
  }

  first = async () => {

  }

  getLGprice = async () => {
        let Web3BSC = await SelectWeb3('0x38')
        let HangQinBSC = await new Web3BSC.eth.Contract(HangQinAbi,global.BSC.hangqin)
        let _priceBSC = await HangQinBSC.methods.PriceByAdr(global.BSC.LG).call()
        let priceBSC =await new BigNumber(web3.utils.fromWei(_priceBSC, "ether")).toFixed(3)

        let Web3HECO = await SelectWeb3('0x80')
        let HangQinHECO = await new Web3HECO.eth.Contract(HangQinAbi,global.HECO.hangqin)
        let _priceHECO = await HangQinHECO.methods.PriceByAdr(global.HECO.LG).call()
        let priceHECO =await new BigNumber(web3.utils.fromWei(_priceHECO, "ether")).toFixed(3)

        let Web3OEC = await SelectWeb3('0x42')
        let HangQinOEC = await new Web3OEC.eth.Contract(HangQinAbi,global.OEC.hangqin)
        let _priceOEC = await HangQinOEC.methods.PriceByAdr(global.OEC.LG).call()
        let priceOEC =await new BigNumber(web3.utils.fromWei(_priceOEC, "ether")).toFixed(3)

        let LGprice = Object.assign({}, this.state.LGprice, { bsc: priceBSC,heco:priceHECO,oec:priceOEC});
        this.setState({LGprice})
    }

  render() {
    let state = this.state
    return (
      <div>
        <Card type="inner" title="" size='small' style={{ borderRadius: '10px' }}>
          <div>
            <img style={{ maxWidth: 'auto', height: '19px', paddingBottom: '2px' }} src={Logo} alt="" />
            LG:
            <img style={{ maxWidth: 'auto', height: '19px', paddingBottom: '2px' }} src={BSCIcon} alt="" />
            &nbsp;{state.LGprice.bsc}&nbsp;
            <img style={{ maxWidth: 'auto', height: '19px', paddingBottom: '2px' }} src={HECOIcon} alt="" />
            &nbsp;{state.LGprice.heco}
            <img style={{ maxWidth: 'auto', height: '19px', paddingBottom: '2px' }} src={OECIcon} alt="" />
            {state.LGprice.oec}
          </div>
        </Card>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(LGearn) 