import React, { Component } from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card, Button, Popover, Input, Progress, Modal } from 'antd';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Loading from '../../../components/loadingReport';
import LG from '../../../ABI/LG';
import { QuestionCircleOutlined } from '@ant-design/icons';
import LGCwindow from '../../../components/LGCwindow';
const BigNumber = require('bignumber.js');

class HBhome extends Component {
  state = {
    requests: [],
    HBid: '',
    HBpassword: '0',
    onoff2: 'none',
    lastHB: '?',
    lastHBsymbol: '',
    visible: false,
    balance: '',
    amount: '',
    lownBalance: '',
    remark: '',
    HBsymbol: '',
    tokenAdr: '',
    percent: '10', //进度条
    percentDisplay: 'block',
    minBlance: '',
    isGet: '',
  };

  async componentDidMount() {

    if (typeof window.ethereum !== 'undefined') {
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try {
        //第一次链接Metamask
        const accounts = await ethereum.enable()

        //初始化Provider
        const provider = window['ethereum']

        if (provider.chainId === '0x1') {
          this.setState({ chainId: "以太链" });
          this.setState({ disabled: "disabled" })
        };

        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts) {
        })

        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion) {
        })

      } catch (e) { }
    } else { console.log('没有metamask') }

    this.setState({ percent: "30", percentDisplay: 'block' });
    const HBtotal = await Hongbao.methods.HBid().call();

    const dataSource2 = []
    for (var i = 0; i <= HBtotal; i++) {
      const HBinfo2 = await Hongbao.methods.getHBinfo(i).call({ from: window.defaultAccount });
      if (HBinfo2[8] === HBinfo2[9]) { HBinfo2[10] = '已' }
      if (HBinfo2[5] !== '0') {
        dataSource2.push(HBinfo2);
        this.setState({ percent: '60' });
      }
    }
    for (var ii = 0; ii < dataSource2.length; ii++) {
      const ERC20 = await new web3.eth.Contract(erc20abi.abi, dataSource2[ii][3]);
      const decimals = await ERC20.methods.decimals().call();
      dataSource2[ii][6] = parseFloat(dataSource2[ii][6] * 10 ** (-decimals)).toFixed(1);
      this.setState({ percent: '90' });
    }
    this.setState({ requests: dataSource2 });
    this.setState({ percent: "100", percentDisplay: 'none' });
  }

  getHB = async () => {
    if (this.state.isGet === '已') {
      this.loading.loadingOpen('none', 'none', 'block', "该红包已领过")
    } else {
      const _LGbalance = await LG.methods.balanceOf(window.defaultAccount).call();
      const LGbalance = await new BigNumber(web3.utils.fromWei(_LGbalance, "ether")).toFixed(0);
      if (Number(LGbalance) < 100) {
        this.loading.loadingOpen('none', 'none', 'block', "您帐户的LG余额小于100")
      } else {
        if (Number(window.usertotal) > Number(LGbalance)) {
          this.loading.loadingOpen('none', 'none', 'block', "您的当前LG余额小于您通过LG每日空投得到的LG总额。请通过LG交易所购回,方可领取红包")
        } else {
          if (Number(window.userLGC) < 10) {
            this.LGCwindow.Open("需申请信用分'LGC'才可抢红包")
          } else {
            const ERC20 = await new web3.eth.Contract(erc20abi.abi, this.state.tokenAdr);
            const _tokenBalance = await ERC20.methods.balanceOf(window.defaultAccount).call();
            const tokenBalance = await new BigNumber(web3.utils.fromWei(_tokenBalance, "ether")).toFixed(0);
            const minBlance = await new BigNumber(web3.utils.fromWei(this.state.minBlance, "ether")).toFixed(0);
            if (Number(tokenBalance) < Number(minBlance)) {
              this.loading.loadingOpen('none', 'none', 'block', `您帐户中的${this.state.HBsymbol}余额少于该红包的要求:${minBlance}${this.state.HBsymbol}`)
            } else {
              await Hongbao.methods.getHB(this.state.HBid, this.state.HBpassword)
                .send({ from: window.defaultAccount })
                .on('transactionHash', (transactionHash) => {
                  if (transactionHash.length !== 0) {
                    this.loading.loadingOpen('block', 'none', 'none', "打包中……")
                  }
                })
                .on('receipt', (receipt) => {
                  if (receipt.status === true) {
                    this.loading.loadingClose()
                  }
                })
                .on('error', (error, receipt) => {
                  if (receipt !== undefined && receipt.status === false) {
                    this.loading.loadingOpen('none', 'none', 'block', "出错！请检查")
                  }
                })
              this.open();
            }
          }
        }
      }
    }
  }

  open = async () => {
    const lastHBadr = await Hongbao.methods.lastTokenAdr(window.defaultAccount).call();
    const ERC20 = new web3.eth.Contract(erc20abi.abi, lastHBadr);
    const lastHBsymbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    this.setState({ lastHBsymbol: lastHBsymbol });
    const lastHB = await Hongbao.methods.lastHB(window.defaultAccount).call();
    if (lastHBsymbol === 'HBTC') {
      const _lastHB = ((parseFloat(web3.utils.fromWei(lastHB, 'Gwei'))) / 10).toFixed(2);
      this.setState({ lastHB: _lastHB });
      this.setState({ lastHBsymbol: '聪BTC' });
    }
    if (lastHBsymbol === 'ETH') {
      const _lastHB = parseFloat(lastHB * 10 ** (-decimals)).toFixed(8);
      this.setState({ lastHB: _lastHB });
    }
    if (lastHBsymbol !== 'ETH' && lastHBsymbol !== 'HBTC') {
      const _lastHB = parseFloat(lastHB * 10 ** (-decimals)).toFixed(2);
      this.setState({ lastHB: _lastHB });
    }
  }

  openintrduce = () => {
    if (this.state.onoff2 === 'none') {
      this.setState({ onoff2: "block" });
    } else {
      this.setState({ onoff2: "none" });
      console.log('00')
    }
  }

  handleCancel = () => {
    this.setState({ visible: false })
  };

  render() {

    const columns = [
      {
        title: '',
        dataIndex: '0',
        key: '0',
        width: 35,
      },
      {
        title: 'Who',
        dataIndex: '1',
        ellipsis: 'true',
        width: 60,
      },
      {
        title: '留言',
        dataIndex: '2',
        ellipsis: 'true',
      },
      {
        title: '剩',
        dataIndex: '5',
        ellipsis: 'true',
        width: 35,
      }
    ];

    const LastHB = []
    LastHB.push(this.state.lastHB, this.state.lastHBsymbol)
    const content2 = (
      <div>
        ID:{this.state.HBid}&emsp;&emsp;{this.state.HBsymbol}&ensp;
        <CopyToClipboard text={this.state.tokenAdr}>
          <Button size='small' type='primary' >复制合约地址</Button>
        </CopyToClipboard>
      </div>
    );

    const title = (
      <div>红包(LG&ge;100+{parseFloat(window.usertotal).toFixed(0)}
        <Popover content='帐户内100LG是必备条件，您通过LG每日空投得到的LG总额' trigger="click">
          <QuestionCircleOutlined />
        </Popover>
        ,&nbsp;LGC&ge;10)</div>
    );

    return (
      <div>
        <Card type="inner" size='small' title={title}
          style={{ background: 'rgb(254, 73, 82)', borderRadius: '10px' }}
          headStyle={{ background: 'rgb(254, 73, 82)', borderRadius: '10px' }}
          extra={
            <Button style={{ background: 'rgba(254, 73, 82,1)', color: 'white', borderColor: 'white' }} >
              <Link to="/HBgive">发红包</Link>
            </Button>
          }
        >
          <div style={{ display: this.state.percentDisplay }}>
            <Progress percent={this.state.percent} status="active" />
          </div>
          <Table size='small'
            pagination={false}
            columns={columns}
            dataSource={this.state.requests}
            rowClassName={record=>record[10]==='已'?'HongBaoHasGet':'HongBaoIsGet'}
            onRow={(record) => {
              return {
                onClick: async () => {
                  this.setState({ visible: true })
                  this.setState({ tokenAdr: record[3] })
                  this.setState({ HBid: record[0] })
                  this.setState({ balance: record[6] })
                  this.setState({ amount: record[5] })
                  this.setState({ lownBalance: parseFloat(web3.utils.fromWei(record[7], 'ether')).toFixed(2) })
                  this.setState({ HBsymbol: record[1] })
                  this.setState({ remark: record[2] })
                  this.setState({ minBlance: record[7] })
                  this.setState({ isGet: record[10] })
                }
              }
            }
            }
          />
        </Card>
        <Card type="inner" size='small' style={{ borderRadius: '10px' }} >
          <Button shape='round' type="dashed" block size="small" onClick={() => { this.openintrduce() }}>说明</Button>
          <div style={{ display: this.state.onoff2 }}>
            <div>1. 持用100或以上LG，是发、领红包的前提条件</div>
            <div>2. 一个帐号只可领取一次</div>
            <div>3. 10%手续费将用于回馈所有LG成员 </div>
          </div>
        </Card>

        <Modal title={content2} visible={this.state.visible}
          onCancel={this.handleCancel}
          footer={null}
        >
          <div style={{
            textAlign: 'center',
            width: '256px',
            margin: '0 auto'
          }}>
            <div className='hongbaoHome'>
              <div style={{
                textAlign: 'center',
                fontSize: '30px',
                color: '#ff006f',
                position: 'relative',
                top: '55px',
              }}
              >
                <b onClick={this.open}>{LastHB}</b>
              </div>
            </div>
            <div style={{
              background: 'rgb(254,73,82)',
              borderBottomLeftRadius: 10,
              borderBottomRightRadius: 10,
            }}>
              <div style={{
                color: 'yellow',
                textAlign: 'left',
                marginLeft: '10px',
                marginRight: '5px',
              }}>
                <div>
                  <span style={{ color: 'black' }}>总余额：</span>
                  {this.state.balance}&nbsp;&nbsp;&nbsp;&nbsp;
                  <span style={{ color: 'black' }}>红包数：</span>
                  {this.state.amount}&nbsp;
                </div>
                <div>
                  <span style={{ color: 'black' }}>平均手气：</span>
                  {parseFloat(this.state.balance / this.state.amount).toFixed(2)}
                </div>
                <div>
                  <span style={{ color: 'black' }}>条  件：</span>
                  帐户内 &ge;{this.state.lownBalance}{this.state.HBsymbol}
                </div>
                <div>
                  <span style={{ color: 'black' }}>发送者：</span>
                  {this.state.userName}
                </div>
                <div>
                  <span style={{ color: 'black' }}>留  言：</span>
                  {this.state.remark}
                </div>
                <div>
                  <span style={{ color: 'black' }}>密码：</span>
                  <Input style={{ width: '70%', backgroundColor: 'rgb(254,73,82)', color: 'white' }}
                    placeholder="输入密码，无密空着"
                    size='small'
                    onChange={event => { this.setState({ HBpassword: event.target.value }) }}
                  />
                </div>
                <div style={{
                  textAlign: 'center'
                }}>

                  <Button shape='circle' size='large'
                    style={{ marginBottom: '5px', marginTop: '5px', background: 'rgba(254, 73, 82,1)', color: 'white', borderWidth: '2px', borderColor: 'white' }}
                    danger
                    onClick={() => { this.getHB() }}>開
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </Modal>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
        <LGCwindow ref={LGCwindow => { this.LGCwindow = LGCwindow }} />
      </div>
    );
  }
}

export default HBhome;
