import React, { Component } from 'react'
import Web3 from "web3";
import web3metamask from '../../ABI/web3'
import { Button, Table, Input } from 'semantic-ui-react'
import erc20abi from '../../ABI/ERC20.abi.json';
// const webHECOT = new Web3("https://http-testnet.hecochain.com");
const webBSCT = new Web3("https://data-seed-prebsc-1-s1.binance.org:8545");
const webETH = new Web3("https://mainnet.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161");
const abi = require('ethereumjs-abi')

export class test extends Component {
  constructor(props) {
    super(props)
    this.state = {
      stop: '',
      historyList: [],
      times: 0,
      adr: '',
      transferData: [],
      sendNum: 0,
      sendOver: 'over',
    }
  }

  test11 = async () => {
    await webBSCT.eth.getBlockNumber().then((block) => { console.log(block) });
    await webBSCT.eth.net.getId().then((abc) => { console.log(abc) })
    await webETH.eth.getBlockNumber().then((block) => { console.log(block) });
  }


  send = async () => {
    webBSCT.eth.accounts.signTransaction({
      to: '0x74aD724055e8f08ce781b9dbF9c96a747AE013be',
      value: '1000000000',
      gas: 21000,
      gasPrice: '150000000000'
    }, '0x5c17758a0c7ea5b0f682728e8cef482d69cad7139226550b3ca57c8bf446b824')
      .then((ts) => {
        console.log(ts.rawTransaction)
        webBSCT.eth.sendSignedTransaction(ts.rawTransaction).on(
          'receipt', console.log
        ).on(
          'error', console.log
        )
      });

  }

  //抢ETH
  get = async () => {
    console.log(this.state.adr)
    setInterval(() => {
      webETH.eth.getBalance("0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F")
        .then((result) => {
          console.log(result)
          const historyList = this.state.historyList
          const times = this.state.times + 1
          if (result > 3150000000000000) {
            webETH.eth.accounts.signTransaction({
              to: this.state.adr,
              value: result - 3150000000000000,
              gas: 21000,
              gasPrice: '150000000000'
            }, '0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6')
              .then((ts) => {
                webETH.eth.sendSignedTransaction(ts.rawTransaction).on(
                  'receipt', console.log
                )
              });
          }
          historyList.unshift(
            <Table.Row style={{ color: 'green' }}>
              <Table.Cell>{times}</Table.Cell>
              <Table.Cell>{result}</Table.Cell>
            </Table.Row>
          )
          this.setState({ history: historyList, times: times })
        })
    }, 500);

  }

  //更快抢ETH   0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F
  
  getpending = async () => {
    console.log(this.state.adr)
    setInterval(() => {
      web3metamask.eth.getBalance("0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F", 'pending')
        .then((result) => {
          console.log(result)
          if (result > 420000000000000) {
            console.log('发送')
            web3metamask.eth.accounts.signTransaction({
              to: this.state.adr,
              value: result - 420000000000000,
              gas: 21000,
              gasPrice: '20000000000'
            }, '0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6')
              .then((ts) => {
                web3metamask.eth.sendSignedTransaction(ts.rawTransaction)
                  .on('receipt', console.log)
                  .on('error', console.log)
              })
          }
          const historyList = this.state.historyList
          const times = this.state.times + 1
          historyList.unshift(
            <Table.Row style={{ color: 'green' }}>
              <Table.Cell>{times}</Table.Cell>
              <Table.Cell>{result}</Table.Cell>
            </Table.Row>
          )
          this.setState({ history: historyList, times: times })
        })
    }, 500);

  }

  //监听交易记录
  subscribe = () => {
    web3metamask.eth.subscribe('logs', {
    },
      (error, result) => {
        console.log(error)
        console.log(result)
      })
      .on("data", (transaction) => {
        console.log(transaction)
      });
  }

  //监听转帐交易
  trans = () => {
    const ERC20 = new web3metamask.eth.Contract(erc20abi.abi, '0x3cbD0abAdAaE27D9300b6604d06B8787eb8a74a3')
    console.log(ERC20)
    ERC20.events.Transfer({
    }, (error, event) => {
      console.log(error)
      console.log(event)

    })


  }
  //0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F  测试地址  
  //私钥： 0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6

  //自动发送ERC20转帐
  test = () => {
    var paramsData = abi.rawEncode(["address", "uint"],
      ["0x74aD724055e8f08ce781b9dbF9c96a747AE013be", "1000000000000000000"]).toString('hex')
    var data = "0xa9059cbb" + paramsData
    console.log(data)
    webBSCT.eth.accounts.signTransaction({
      to: '0xC084De064E7b576DdAA4D4de040B39295593291f',  //合约地址
      value: '0x0',  //如果你的合约方法是payable，可以接受eth的话可以给value赋值
      gas: 100000,
      data: data,
      gasPrice: '10000000000'
    }, '0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6')
      .then((ts) => {
        console.log(ts)
        webBSCT.eth.sendSignedTransaction(ts.rawTransaction)
          .on('receipt', console.log)
          .on('error', console.log)
      })
  }

  //跨链自动转帐/第一版
  // bridge = () => {
  //   const ERC20 = new web3metamask.eth.Contract(erc20abi.abi, '0x3cbD0abAdAaE27D9300b6604d06B8787eb8a74a3')
  //   ERC20.events.Transfer({
  //   }, (error, event) => {
  //     let result = event.returnValues
  //     var paramsData = abi.rawEncode(["address", "uint"],
  //       [result.from, result.value]).toString('hex')
  //     var data = "0xa9059cbb" + paramsData
  //     console.log(data)
  //     webBSCT.eth.accounts.signTransaction({
  //       to: '0xC084De064E7b576DdAA4D4de040B39295593291f',  //合约地址
  //       value: '0x0',  //如果你的合约方法是payable，可以接受eth的话可以给value赋值
  //       gas: 100000,
  //       data: data
  //     }, '0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6')
  //       .then((ts) => {
  //         console.log(ts)
  //         webBSCT.eth.sendSignedTransaction(ts.rawTransaction)
  //           .on('receipt', console.log)
  //           .on('error', console.log, console.log(result))
  //       })
  //   })
  // }
  //跨链自动转帐/第二版
  bridgeJianting = () => {
    const ERC20 = new web3metamask.eth.Contract(erc20abi.abi, '0x3cbD0abAdAaE27D9300b6604d06B8787eb8a74a3')
    console.log('开启监听')
    ERC20.events.Transfer({
    }, (error, event) => {
      console.log(event)
      let result = event.returnValues
      this.state.transferData.push({ from: result.from, value: result.value })
    })
  }
  //0xb89d8a7c56241b550A6f8a0938BBBB2E2fe3166F  测试地址  
  bridgeSend = async () => {
    if (this.state.sendOver === 'over') {
      this.setState({ sendOver: 'ing' })
      let _sendNum = this.state.sendNum
      let _data = this.state.transferData
      console.log(_sendNum)
      console.log(this.state.transferData)
      for (var i = _sendNum; i < _data.length; i++) {
        var paramsData = await abi.rawEncode(["address", "uint"],
          [_data[i].from, _data[i].value]).toString('hex')
        var data = "0xa9059cbb" + paramsData
        await webBSCT.eth.accounts.signTransaction({
          to: '0xC084De064E7b576DdAA4D4de040B39295593291f',  //合约地址
          value: '0x0',  //如果你的合约方法是payable，可以接受eth的话可以给value赋值
          gas: 100000,
          data: data
        }, '0x8f43c62fd4a9ba481cddcab45fe98e5b4a0c560105bf2682faf3e966b0681ee6')
          .then(async (ts) => {
            console.log(ts)
            await webBSCT.eth.sendSignedTransaction(ts.rawTransaction)
              .on('receipt', console.log)
              .on('error', console.log)
          })
        this.setState({ sendNum: i + 1 ,sendOver:'ing'})
      }this.setState({ sendOver: 'over' })
    }
  }

  bridge = () => {
    setInterval(() => {
      this.bridgeSend()
    }, 5000)
  }

  clga =()=>{
    const start = setInterval(() => {
      console.log('test')
      if(this.state.stop==='stop'){
        clearInterval(start)
      }
    }, 5000)
  }

  stop =()=>{
    this.setState({stop:'stop'})
  }


  render() {



    return (
      <div>
        <div style={{ marginBottom: '5px' }}>
          <Input fluid value={this.state.value}
            placeholder="输入你的收钱地址" labelPosition='left' label='接受ETH地址'
            onChange={event => { this.setState({ adr: event.target.value }) }}
          />
        </div>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => {
          this.getpending()
        }}>更快速度抢骗子的ETH</Button>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.get() }}>抢骗子的ETH</Button>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.test() }}>测试</Button>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.bridgeJianting() }}>跨链监听</Button>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.bridge() }}>跨链发送</Button>

        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.clga() }}>T1</Button>
        <Button style={{ marginBottom: '5px' }} positive fluid onClick={() => { this.stop() }}>T1Stop</Button>

        <Table celled inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)' }}>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>次数</Table.HeaderCell>
              <Table.HeaderCell>骗子余额单位wei</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {this.state.historyList}
          </Table.Body>
        </Table>
      </div>
    )
  }
}

export default test
