import React,{ Component }  from 'react';
import '../../../App.css';
import { Link } from 'react-router-dom';
import { Card ,Button,Input,Popover,Divider} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';

class App extends Component {
      state = {
        user:'',
        chainId:''
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']

        //获取网络ID
        console.log(provider.chainId)
        if(provider.chainId !== '0x80'){
          this.setState({
            chainIdDisabled:"disabled",
            message:"您当前帐户非火币链，请切换至火币链"
          })};
        if(provider.chainId === '0x80'){
          this.setState({ chainId:"火币链"})
        }
        if(provider.chainId === '0x100'){
          this.setState({ chainId:"火币测试链"})
        }
          
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        this.setState({tokenOwnerAddress:window.defaultAccount});
        this.setState({user:window.defaultAccount});
      }catch(e){}
    }else{console.log('没有metamask')}

  }

  render(){

      const content = (
        <div>
          <p>您的当前帐号，方便您核对</p>
        </div>
      );

      const content1 = (
        <div>
          <p>什么是准备期？
            <div>&emsp;&emsp;创建好后初始持有人可指定部分帐户可交易，
              待准备工作（比如部署去中心化交易所）完成后，
              再开放全部交易。
              这样可以有效防止机器人抢买行为。
            </div>
          </p>
          <p>在准备期内，非指定帐户不可以买卖。</p>
          <p>一旦开放全部交易之后，就不可以再回到准备期</p>
          <p>可销毁是指初始持有人仅可以对自己帐户内的通证进行部分销毁</p>
        </div>
      );


  return (
    <div>
        <Card type="inner" size='small'  >
              <div >
                <div>当前链：{this.state.chainId}</div>
                <Input  id='i1' addonBefore="帐号" value={this.state.user} 
                    suffix={
                      <Popover  content={content} trigger="click">
                      <QuestionCircleOutlined id='i1' /> 
                      </Popover>
                    }
                    disabled/>
              </div>

          <Divider>请选择类型</Divider>
              <div>
                <Link to="/TokenFactory">
                    <Button block type='primary' size='large'>总量固定型ERC20通证</Button>
                </Link>
                <Link to="/TkFactoryDDXH">
                    <Button style={{marginTop:'20px'}} block type='primary' size='large' 
                    >含准备期与销毁功能ERC20通证
                        <Popover content={content1} trigger="click">
                            <QuestionCircleOutlined />  
                        </Popover>
                    </Button>
                </Link>
              </div>
        

         </Card>
    </div>
  );
}
}

export default App;
