import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Card ,Button,Input} from 'antd';
import { CopyOutlined} from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Invite from '../../ABI/invite';
import LGC from '../../ABI/LGC';

class Hangqinadd extends Component {
      state = {
        message:'',
        adr:'',
        LGC:'',
        HT:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  }

  getInviter = async () =>{
    const inviter = await Invite.methods.getInviter(this.state.adr).call();
    const balance = await LGC.methods.balanceOf(this.state.adr).call();
    const HT = await web3.eth.getBalance(this.state.adr);
    this.setState({message:inviter});
    this.setState({LGC:balance});
    this.setState({HT:web3.utils.fromWei(HT,'ether')});
  }
  
  render(){

  return (
    <div>
        <Card type="inner" size='small' title="超级用户特殊操作" >
                
            <Input  addonBefore='查推荐人'  placeholder="输入帐号"
                  onChange={event=>{this.setState({adr:event.target.value})}}
                  suffix={
                      <Button type='primary' size="small"
                        onClick={this.getInviter}
                        >查询</Button>
                  }
            />
            <div style={{color:"green",fontSize:"13px",marginTop:'10px'}}>
                 推荐人：{this.state.message}
                <CopyToClipboard text={this.state.message}>
                    <Button size='small'><CopyOutlined /></Button>
                </CopyToClipboard>
                <br/>
                HT：{this.state.HT}<br/>
                LGC：{this.state.LGC}
            </div>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
