import web3more from '../../ABI/web3more';
import web3 from '../../ABI/web3';
import erc20abi from '../../ABI/ERC20.abi.json';
const BigNumber = require('bignumber.js');

export const ChangeChain = async (id) => {
  switch (id) {
    case 'HECO':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x80',
          chainName: 'HECO',
          nativeCurrency: {
            name: 'HT',
            symbol: 'HT',
            decimals: 18,
          },
          rpcUrls: ['https://http-mainnet.hecochain.com'],
          blockExplorerUrls: ['https://hecoinfo.com/'],
        }],
      })
      return ["HECO", '0x80', '']

    case 'HECOT':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x100',
          chainName: 'HECOT',
          nativeCurrency: {
            name: 'HT',
            symbol: 'HT',
            decimals: 18,
          },
          rpcUrls: ['https://http-testnet.hecochain.com'],
          blockExplorerUrls: ['https://testnet.hecoinfo.com/'],
        }],
      }); return ["HECOT", '0x100', '']

    case 'BSC':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x38',
          chainName: 'BSC',
          nativeCurrency: {
            name: 'BNB',
            symbol: 'BNB',
            decimals: 18,
          },
          rpcUrls: ['https://bsc-dataseed.binance.org/'],
          blockExplorerUrls: ['https://bscscan.com'],
        }],
      }); return ["BSC", '0x38', '']

    case 'BSCT':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x61',
          chainName: 'BSC',
          nativeCurrency: {
            name: 'tBNB',
            symbol: 'tBNB',
            decimals: 18,
          },
          rpcUrls: ['https://data-seed-prebsc-1-s1.binance.org:8545'],
          blockExplorerUrls: ['https://testnet.bscscan.com'],
        }],
      }); return ["BSCT", '0x38', '']

    case 'OEC':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x42',
          chainName: 'OEC',
          nativeCurrency: {
            name: 'OKT',
            symbol: 'OKT',
            decimals: 18,
          },
          rpcUrls: ['https://exchainrpc.okex.org'],
          blockExplorerUrls: ['https://www.oklink.com/okexchain'],
        }],
      }); return ["OEC", '0x38', '']

    case 'OECT':
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [{
          chainId: '0x41',
          chainName: 'OECT',
          nativeCurrency: {
            name: 'OKT',
            symbol: 'OKT',
            decimals: 18,
          },
          rpcUrls: ['https://exchaintestrpc.okex.org'],
          blockExplorerUrls: ['https://www.oklink.com/okexchain-test'],
        }],
      }); return ["OECT", '0x41', '']
      default:
        break;
  }
}



export const SelectWeb3 = async (chainid) => {
  let CurWeb3
  switch (chainid) {
    case '0x80': CurWeb3 = web3more.web3HECO; break;
    case '0x38': CurWeb3 = web3more.web3BSC; break;
    case '0x42': CurWeb3 = web3more.web3OEC; break;
    case '0x100': CurWeb3 = web3more.web3HECOT; break;
    case '0x61': CurWeb3 = web3more.web3BSCT; break;
    case '0x41': CurWeb3 = web3more.web3OECT; break;
    case '0x03': CurWeb3 = web3more.web3Ropsten; break;
    default:
      break;
  }
  return CurWeb3
}

export const isApprove = async (tokenAdr, spender) => {
  if(tokenAdr==="0x0000000000000000000000000000000000000000"){
    return 'yes'
  }
  const accounts = await window.ethereum.enable()
  const ERC20 = new web3.eth.Contract(erc20abi.abi, tokenAdr)
  const _allowance = await ERC20.methods.allowance(accounts[0], spender).call();
  const allowance = new BigNumber(web3.utils.fromWei(_allowance, 'ether')).toFixed(0);
  if (allowance < 100000000) {
    return 'no'
  } else {
    return 'yes'
  }
}

export const getChainInfo = async (chainid) => {
  let ChainName
  let ChainID
  let ChainAdr
  switch (chainid) {
    case '0x80': ChainName = 'HECO'; ChainID = '0x80'; ChainAdr = global.HECO; break;
    case '0x38': ChainName = 'BSC'; ChainID = '0x38'; ChainAdr = global.BSC; break;
    case '0x42': ChainName = 'OEC'; ChainID = '0x42'; ChainAdr = global.OEC; break;
    case '0x100': ChainName = 'HECOT'; ChainID = '0x100'; ChainAdr = global.HECOT; break;
    case '0x61': ChainName = 'BSCT'; ChainID = '0x61'; ChainAdr = global.BSCT; break;
    case '0x41': ChainName = 'OECT'; ChainID = '0x41'; ChainAdr = global.OECT; break;
    default: case '0x00': ChainName = '选择链'; ChainID = '0x00'; ChainAdr = '0';
  }
  return { 'ChainName': ChainName, 'ChainID': ChainID, 'ChainAdr': ChainAdr }
}

export const getLGbalance = async () => {
  let LGadr
  let balance
  await getChainInfo(window.ethereum.chainId).then((result) => {
    LGadr = result.ChainAdr.LG
  })
  const ERC20 = new web3.eth.Contract(erc20abi.abi, LGadr)
  const _balance = await ERC20.methods.balanceOf(window.defaultAccount).call()
  balance = parseFloat(web3.utils.fromWei(_balance, "ether")).toFixed(0)
  return balance 
}

